<template>
  <div id="app">
    <Nav :navVal="activeNav"></Nav>
    <keep-alive exclude="movieDetail,bookDetail">
      <router-view @changeNav="change"/>
    </keep-alive>
  </div>
</template>

<script>
import Nav from '@/components/Navigation.vue'

export default {
  data() {
    return {
      activeNav: {
        title: '电影',
        className: 'movie'
      }
    }
  },
  components: {
    Nav
  },
  methods: {
    change(obj) {
      this.activeNav.title = obj.title;
      this.activeNav.className = obj.className;
    }
  },
}
</script>

<style>
  #app{
    padding: 1rem 0;
  }
  *{
    margin: 0;
  }
</style>
