<template>
  <div :class="['navigation',activeClass]">
    <header>
      <span class="main-page" @click="changebtnHandle(navList[0])">首页</span>
      <span class="content">{{title}}</span>
    </header>
    <nav>
      <ul>
        <li
          v-for="(item,index) in navList"
          :key="index"
          @click="changebtnHandle(item)"
          :class="{active:activeClass==item.path.substr(1)}"
        >{{item.name}}</li>
      </ul>
    </nav>
  </div>
</template>
<script>
export default {
  data() {
    return {
      title: "电影",
      activeClass: "movie",
      navList: [
        {
          name: "电影",
          path: "/"
        },
        {
          name: "音乐",
          path: "/music"
        },
        {
          name: "书籍",
          path: "/book"
        },
        {
          name: "聊天",
          path: "/chat"
        }
      ]
    };
  },
  props: ['navVal'],
  methods: {
    changebtnHandle(item) {
      this.$router.push(item.path);
      this.title = item.name;
      this.activeClass = item.path.substr(1);
    }
  },
  created() {
    
  },
  //监听传过来的navVal对象 不要在计算属性中写表达式 虽然可以 但违背了初衷
  computed: {
    propVal(){
      return this.navVal.title;
    }
  },
  //watch只能监听基本数据类型 写逻辑表达式 监听propVal的变化 然后传值
  watch: {
    propVal(){
      this.title = this.navVal.title;
      this.activeClass = this.navVal.className;
    }
  },
};
</script>
<style scoped>
* {
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
}
li {
  list-style: none;
}
header {
  position: fixed;
  top: 0;
  height: 1rem;
  width: 100%;
  background-color: rgb(33, 150, 243);
  z-index: 10;
}
nav {
  position: fixed;
  bottom: 0;
  height: 1rem;
  width: 100%;
  background-color: rgb(33, 150, 243);
  z-index: 10;
}
.navigation {
  font-size: 0.3rem;
  color: #ddd;
}
.main-page {
  padding-left: 5px;
  line-height: 1rem;
}
header .content {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  line-height: 1rem;
  font-size: 0.5rem;
}
nav ul {
  width: 100%;
  display: flex;
}
nav ul li {
  float: left;
  line-height: 1rem;
  flex-grow: 1;
  text-align: center;
}
.movie header,
.movie nav {
  background-color: rgb(33, 150, 243);
}
.music header,
.music nav {
  background-color: rgb(0, 150, 136);
}
.book header,
.book nav {
  background-color: rgb(121, 85, 72);
}
.chat header,
.chat nav {
  background-color: rgb(63, 81, 181);
}
nav li.active {
  color: #fff;
  font-weight: border;
  font-size: 0.4rem;
}
</style>
